"use client";

import { useEffect, useState } from "react";
import { fetchQuote } from "@/lib/fetchQuote";
import QuoteCard from "@/components/QuoteCard";
import ModeToggle from "@/components/ModeToggle";
import Loader from "@/components/Loader";
import MyButton from "@/components/Button";

export default function Home() {
  const [quote, setQuote] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  const loadQuote = async () => {
    setLoading(true);
    setError("");
    try {
      const q = await fetchQuote();
      setQuote(q);
    } catch {
      setError("Unable to fetch quote. Try again.");
    } finally {
      setLoading(false);
    }
  };

  const handleCopy = async () => {
    if (quote) {
      await navigator.clipboard.writeText(`“${quote.content}” — ${quote.author}`);
      alert("Copied to clipboard!");
    }
  };

  const handleShare = () => {
    if (navigator.share && quote) {
      navigator.share({
        title: "Quote",
        text: `"${quote.content}" — ${quote.author}`,
      }).catch(console.error);
    } else {
      alert("Share not supported");
    }
  };

  useEffect(() => {
    loadQuote();
  }, []);

  return (
    <main className="min-h-screen bg-base-200 flex flex-col items-center justify-start p-6">
      <header className="w-full flex justify-between items-center mb-6 max-w-lg">
        <h1 className="text-3xl font-bold">Inspire Me</h1>
        <ModeToggle />
      </header>

      {loading && <Loader />}
      {error && <p className="text-error">{error}</p>}
      {quote && (
        <QuoteCard
          quote={quote}
          onCopy={handleCopy}
          onShare={handleShare}
        />
      )}

      <MyButton onClick={loadQuote} className="mt-6">
        New Quote
      </MyButton>
    </main>
  );
}
