import MyButton from "./Button";
import { Copy, Share2 } from "lucide-react";

export default function QuoteCard({ quote, onCopy, onShare }) {
  return (
    <div className="card shadow-lg bg-base-100 w-full max-w-lg">
      <div className="card-body">
        <p className="italic">“{quote.content}”</p>
        <p className="text-right text-sm mt-2">— {quote.author}</p>
        <div className="card-actions flex justify-between mt-4">
          <MyButton onClick={onCopy}>
            <Copy className="mr-2 w-4 h-4" /> Copy
          </MyButton>
          <MyButton onClick={onShare}>
            <Share2 className="mr-2 w-4 h-4" /> Share
          </MyButton>
        </div>
      </div>
    </div>
  );
}
